.kb-event-container{
  width: 100%;
  max-width: 800px;
  margin: auto;

  .kb-manabu-logo {
    height:100px;
    width:auto;
    display: block;
    margin: auto;
  }

  .submit-btn {
    // @include button($color: #2762FB, $text_color: white);
    width: 100%;
    max-width: 250px;
    &:disabled{
      pointer-events: none;
      cursor:not-allowed !important;
      opacity:0.8;
      color: #373943!important;
      text-shadow: 0 1px 1px rgba(0,0,0,0);
      border: solid 1px #dcdde6;
      background: #dcdde6;
    }
  }

}
.kb-event-progress{
  height: 6px;
  border-radius: 40px;
  width: 100%;
  background-color: #f4f4f7;
  span{
    height: 6px;
    border-radius: 40px;
    background-color: #2962ff;
    display: block;
  }
}
.kb-event-small{
  font-size: 11px;
}
.kb-event-contents{
  width: 100%;
}
.kb-event-item{
  border-bottom: solid 1px #efefef;
  &:last-child{
    border-bottom: 0;
  }
}
.kb-event-btn{
  flex-shrink: 0;
}
.kb-event-type{
  display: flex;
  align-items: center;
  &:before{
    content: "";
    width: 9px;
    height: 9px;
    margin: 0 10px 0 0;
    border-radius: 10px;
    background-color: #7cb341;
  }
}
.kb-event-type-manabu{
  &:before{
    background-color: #2196f3;
  }
}
.kb-event-type-tameru{
  &:before{
    background-color: #f44336;
  }
}
.kb-event-alert{
  position: relative;
  color: #f44336;
  &:before{
    content: "締め切りをすぎています";
    position: absolute;
    word-break: keep-all;
    left: 50%;
    transform: translateX(-50%);
    color: #fff;
    bottom: 100%;
    padding: 15px 10px 8px;
    background-image: url('/static/ver2.1/img/2064@3x.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
  }
}
